
import { defineComponent, PropType } from "vue";
import FormInput from "./FormInput.vue";
import { UserInfoInputSetting } from "@/myInterface";

export default defineComponent({
  props: {
    fieldsSettings: {
      type: Array as PropType<UserInfoInputSetting[]>,
      required: true,
    },
    formData: {
      type: Object as any,
      required: true,
    },
    pageName: {
      type: String,
      required: true,
    },
    buttonName: {
      type: String,
      required: true,
    },
    messageShowed: {
      type: String,
      required: true,
    },
    messageType: {
      type: String as PropType<"success" | "warning">,
      required: true,
    },
  },
  components: { FormInput },
  emits: ["input", "clickSubmitButton"],
  data() {
    return {
      publicPath:
        process.env.NODE_ENV === "production" ? "/my_online_shop/" : "/",
      formInputDisplayType: "table" as string,
    };
  },
  methods: {
    updateValue(payload: { inputName: string; value: string }): void {
      this.$emit("input", {
        inputName: payload.inputName,
        value: payload.value,
      });
    },
  },
});
